.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 21px;
  color: white;
}

.dayHeader {
  position: absolute;
  top: 50px;
  height: fit-content;
}

.daySelector {
  width: 150px;
  color: #282c34;
}

.dayLabel {
  float: left;
  width: 50px;
  display: flex;
  margin-right: 20px;
  position: relative;
  font-size: 31px;
}

.fullDay {
  position: fixed;
  left: 11vw;
  width: 20vw;
}

.list {
  list-style-type: none;
}

.listTime {
  list-style-type: none;
}

.nextCurrent {
  position: fixed;
  width: 38vw;
} 

.tmrw {
  position: fixed;
  right: 11vw;
  width: 20vw;
}

.listDiv {
  border: 2px;
  border-style: solid;
  border-color: white;
  margin-top: 5px;
  padding: 10px;
}

.App-link {
  color: #61dafb;
}
